@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Gilroy-medium.woff2") format("woff2"), url("../fonts/Gilroy-medium.woff") format("woff"); }

@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Gilroy-bold.woff2") format("woff2"), url("../fonts/Gilroy-bold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-body {
  min-height: 100%;
  min-width: 375px;
  background-color: #ffffff;
  font-family: "Gilroy", "Arial", sans-serif;
  color: #000000; }
  .page-body--open-menu {
    overflow-y: hidden; }

.container {
  padding: 0 calc(40px + (252 - 40) * ((100vw - 1024px) / (1445 - 1024))); }

.page-header {
  padding: 0 0 50px; }

.navigation__list {
  margin: 0;
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 54px 0 31px;
  border-bottom: 1px solid #828282; }
  .navigation__list a {
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    text-decoration: none;
    color: #828282; }
    .navigation__list a:hover {
      color: #000000; }
    .navigation__list a:active, .navigation__list a:focus {
      color: #000000;
      filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.4)); }

.base__header {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding: 15px 0; }
  .base__header h2 {
    font-weight: bold;
    font-size: 34px;
    line-height: 42px;
    width: 194px;
    margin: 0; }
  .base__header p {
    width: 209px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    margin: 0 0 12px; }

.base__lang {
  display: flex;
  transform: rotate(-90deg);
  width: 23px;
  padding: 0;
  margin: 0 0 12px; }

.base__ru-lang,
.base__eng-lang {
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #828282;
  border: none;
  background-color: transparent; }

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f6f6f6;
  padding: 50px 0 80px; }
  .about p {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    width: 486px; }
  .about h2 {
    font-weight: bold;
    font-size: 34px;
    line-height: 42px;
    text-align: center; }

.skills h2 {
  font-weight: bold;
  font-size: 34px;
  line-height: 42px;
  text-align: center; }

.skills__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }

.skills__item {
  display: grid;
  justify-items: center;
  align-content: end;
  width: 130px;
  height: 160px;
  margin: 30px 20px; }
  .skills__item h3 {
    font-weight: bold;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #828282;
    width: 120px;
    height: 40px; }

.contacts {
  display: grid;
  justify-items: center;
  align-content: center;
  grid-gap: 20px;
  padding: 20px 0; }
  .contacts h2 {
    font-weight: bold;
    font-size: 34px;
    line-height: 42px;
    text-align: center;
    margin: 0; }

.contacts__mail,
.contacts__tel {
  font-weight: bold;
  font-size: 18px;
  line-height: 22px;
  text-decoration: none;
  color: #828282; }
  .contacts__mail:hover,
  .contacts__tel:hover {
    color: #000000; }
  .contacts__mail:active, .contacts__mail:focus,
  .contacts__tel:active,
  .contacts__tel:focus {
    color: #000000;
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.4)); }

.contacts__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center; }

.contacts__item {
  margin: 0 10px; }
  .contacts__item a {
    box-sizing: border-box;
    padding: 10px; }
    .contacts__item a:hover svg {
      fill: #000000; }
    .contacts__item a:active svg, .contacts__item a:focus svg {
      fill: #000000;
      filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.4)); }
